@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css2?family=Cinzel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;1,400;1,700&display=swap);
body {
  margin: 0;
  font-size: 10pt;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media print {
  body {page-break-after: always;}
}
.App {
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: "10pt";
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
    font-family: 'Lato', serif;
}

.Name {
    font-family: 'Cinzel', serif;
    font-size: 24pt;
    font-weight: bold;
    width: 100%;
    text-align: center;
    border-bottom: 3pt solid #333;
    margin-bottom: 0.5em;
    padding: 0.5em;
    background-color: #f6f6f6;
}

.Personal svg {
    padding-right: 0.5em;
    color: #333;
}

.PersonalItem {
    align-items: center;
    display: inline-flex;
    margin-bottom: 0.5em;
}

.Section {
    margin-bottom: 1em;
}

.Section .Title {
    display: flex;
    font-family: 'Cinzel', serif;
    font-size: 14pt;
    font-weight: bold;
    width: 100%;
    border-bottom: 1px solid #333;
    margin-bottom: 0.5em;
    background-color: #f6f6f6;
    padding: 0.25em;
}

.Degree {
    margin-bottom: 1em;
}

.Skills>div {
    margin-bottom: 1em;
}

.twoColumn {
    display: flex
}

.twoColumn>span:nth-child(1) {
    width: 100%;
}

.twoColumn>span:nth-child(2) {
    width: 100%;
    text-align: right;
}

.primary.twoColumn>span {
    padding: 0.5em;
}

.primary.twoColumn>span:nth-child(1) {
    width: 22em;
    padding-right: 1em;
}
.primary.twoColumn>span:nth-child(2) {
    padding-left: 1em;
}

.Personal .twoColumn span:nth-child(2) {
    width: auto;
    text-align: left;
}


@media print{
    @page {
        size: letter;
        margin: 0cm;
        margin-top: 2cm;
        margin-bottom: 2cm;
    }
    @page :first {
        margin-top: 0cm;
    }
    html, body {
        height:100vh; 
        margin: 0 !important; 
        padding: 0 !important;
    }
    .primary {
        padding: 2cm;
        padding-top: 0cm;
    }
    .Name {
        padding-top: 1cm;
    }
    div.experience {
        page-break-inside: avoid;
        -webkit-column-break-inside: avoid;
                break-inside: avoid;
    }
}

@media screen {
    .primary {
        padding: 5em;
        padding-top: 0em;
    }
    .Name {
        padding-top: 2em;
    }
}

